ge itself is the key identifier for Apple. Similarly, a service mark follows the same features but is different because it is used to identify a service. An example of a well-known\n                    service mark is McDonald's whose mark is a golden 'M' which is globally recognized by all ages since 1955. Additionally, in certain circumstances, trade/service marks can also involve colours, music, and smell. For example, Coca-Cola\n                    is well known for its red and white colour combination, and Nokia is known for its unique ringtone.\n                  ")]),_c('b',[_vm._v("Property and Intellectual Property")]),_vm._v(" "),_c('p',[_vm._v("'Intellectual property,' however, has no physical embodiment that one can see, touch or feel such as literature and music. Trade names, artistic designs and digital programming are also part of the intellectual property band. Like physical\n                    property, the intellectual property can be owned, purchased, sold, transferred, and/or licensed. Systems of ownership of intellectual property are relatively new historically.\n                  ")]),_c('b',[_vm._v("Trademark Rights")]),_vm._v(" "),_c('p',[_vm._v("Trademarks adopt rights in two ways: 1) by registration, and 2) by using the mark first. Both ways can be protected. By registering, you will gain many advantages from the federal law, including the use of the  symbol on your trademark.\n                    You will also have the ability to show others of your ownership to the brand, making it less likely for others to consume. Under common law, being the first to use a specific trademark also comes with certain rights that may stop others\n                    from using your mark. However, this can be risky because it does not come with as many benefits as a registered trademark would.")]),_vm._v(" "),_c('p',[_c('b',[_vm._v("TIP")])]),_vm._v(" "),_c('p',[_vm._v("The more unique a trademark is, the less chance it has for it to be duplicated, which m