/* harmony default export */ var nicheSingle = ({
  data: function data() {
    return {
      niche: '',
      sent: ''
    };
  },
  mounted: function mounted() {
    var vm = this;
    axios_default.a.post(url + 'niche-service/' + this.$route.params.nicheId).then(function (response) {
      vm.niche = response.data[0];
    }).catch(function (error) {
      console.log(error);
    });
    $(document).ready(function () {
      var serviceId = $('.serviceVal').val();
      var lsId = localStorage.getItem('quoteSent');
      console.log(serviceId);
      if (lsId = serviceId) {
        console.log('ho ho ho');
        vm.sent = true;
      }
      function submitForm() {
        $.ajax({
          type: "GET",
          url: url + 'quote',
          data: {
            'service': $('.serviceVal').val(),
            'name': {
              'first': $("#firstName").val(),
              'last': $("#lastName").val()
            },
            'email': $("#email").val(),
            'phone': $('#phone').val(),
            'quoteType': $("#quoteType").val()
          },
          success: function success(data) {
            vm.sent = true;
            localStorage.setItem('quoteSent', serviceId);
          },
          error: function error(data) {
            console.log(data);
            vm.sent = false;
          }
        });
      }
      // Validation
      $("#contactForm").validate({
        rules: {
          "nameFirst": {
            required: true,
            minlength: 2
          },
          "nameLast": {
            required: true,
            minlength: 2
          },
          "phone": {
            required: true,
            digits: true
          },
          "email": {
            required: true,
            email: true
          },
          "quoteType": {
            required: true
          }
        },
        messages: {
          "nameFirst": {
            required: "Please, enter a first name"
          },
          "nameLast": {
            required: "Please, enter a last name"
          },
          "quoteType": {
            required: "Please choose a service"
          },
          "phone": {
            required: "Please, enter a phone"
          },
          "email": {
            required: "Please, enter an email",
            email: "Email is invalid"
          }
        },
        submitHandler: function submitHandler(form) {
          // for demo
          submitForm();
          return false; // for demo
        }
      });
    });
  }
});
