<script>
    import axios from 'axios'
    import * as config from '@/scripts/main'

    export default {
        data() {
            return {
                message: '',
            }
        },
        mounted() {
            var vm = this;
            function saveFormToLS() {
              vm.message = 'sent';
              if (message) {
                var xxxx = document.getElementById("contact-head");
                var successMessage = document.getElementById("success-message");
                successMessage.style.display = "block";
                xxxx.style.display = "none";
              }
            }
            function submitForm() {
                $.ajax({
                    type: "GET",
                    url: config.url + 'contact',
                    data: {
                        "title": $("#title").val(),
                        "name": $("#name").val(),
                        "email": $("#email").val(),
                        "phone": $("#phone").val(),
                        "country": $("#country").val(),
                        "subject": $("#subject").val(),
                        "message": $("#message").val()
                    },
                    success: function (msg) {
                        saveFormToLS();
                        console.log(msg);
                    },
                    error: function (msg) {
                        console.log('greska' + msg);
                    }
                });
            };
          $(document).ready(function () {
            $("#contactForm").validate({
              rules: {
                "name": {
                  required: true,
                  minlength: 5
                },
                "title": {
                  required: true,
                },
                "phone": {
                  required: true,
                  digits: true
                },
                "country": {
                  required: true
                },
                "subject": {
                  required: true,
                  minlength: 5
                },
                "message": {
                  required: true,
                  minlength: 10
                },
                "email": {
                  required: true,
                  email: true
                }
              },
              messages: {
                "name": {
                  required: "Please, enter a name"
                },
                "phone": {
                  required: "Please, enter a phone"
                },
                "country": {
                  required: "Please, choose your country"
                },
                "subject":{
                  required: "Please, enter a subject"
                },
                "message": {
                  required: "Please, enter a message"
                },
                "email": {
                  required: "Please, enter an email",
                  email: "Email is invalid"
                }
              },
              submitHandler: function (form) { // for demo
                submitForm();
                return false; // for demo
              }
            });


          });
        }
    }
</script>



